jQuery(function($) {
    spinner = new Spinner({
        lines: 0,
        length: 0,
        width: 0,
        radius: 0,
        corners: 0,
        rotate: 0,
        direction: 1,
        color: '#000',
        speed: 1.1,
        trail: 74,
        shadow: false,
        hwaccel: true,
        className: 'spinner',
        zIndex: 2e9,
        top: '50%',
        left: '50%',
        position: 'absolute'
    });

    var timer = null;
    $('input#price-filter').change(function(e) {
        timer && clearTimeout(timer);
        timer = setTimeout(updateQuery.bind(null, 'price', this.value), 500);
    });

    if(typeof(URLSearchParams) !== 'undefined') {
        $('a.open-cat').click(function(e) {
            e.preventDefault();
            var params = {};
            new URLSearchParams(window.location.search).forEach(function(value, key) {
                if(key !== 'cat') {
                    params[key] = value;
                }
            });

            window.location.href = this.href + '?' + $.param(params);
        });
    }
});

var mobiledFilterClosed =0;
/* Set the width of the side navigation to 250px and the left margin of the page content to 250px and add a black background color to body */
function openSNav() {
    document.getElementById("narrow-results").style.width = "100%";
    $('.filters').css('display', '');
}

/* Set the width of the side navigation to 0 and the left margin of the page content to 0, and the background color of body to white */
function closeSNav() {
    document.getElementById("narrow-results").style.width = "0";
    mobiledFilterClosed =1;
}

$(document).ready(function(){
    $(window).resize(function() {
        // console.log(mobiledFilterClosed);
        if ($(window).width() > '769' && mobiledFilterClosed) {
            document.getElementById("narrow-results").style.width = "100%";
            mobiledFilterClosed = 0;
        }
    });
});
var global   = typeof(window) !== 'undefined' ? window : this;
function updateQueryStringParameter(uri, key, value) {
    var re = new RegExp("([?&])" + key + "=.*?(&|$)", "i");
    var separator = uri.indexOf('?') !== -1 ? "&" : "?";
    if (uri.match(re)) {
        return uri.replace(re, '$1' + key + "=" + value + '$2');
    }
    else {
        return uri + separator + key + "=" + value;
    }
}

var bahook = new Sonic({

    width: 160,
    height: 80,
    padding: 10,

    stepsPerFrame: 4,
    trailLength: 1,
    pointDistance: .03,

    strokeColor: '#febe1b',

    step: 'fader',

    multiplier: 3,

    setup: function() {
        this._.lineWidth = 5;
    },

    path: [

        ['arc', 10, 10, 10, -270, -90],
        ['bezier', 10, 0, 40, 20, 20, 0, 30, 20],
        ['arc', 40, 10, 10, 90, -90],
        ['bezier', 40, 0, 10, 20, 30, 0, 20, 20]
    ]

});

bahook.play();

//        document.body.appendChild(circle.canvas);
document.getElementsByClassName("loading")[0].appendChild(bahook.canvas);
document.getElementById("cover").appendChild(bahook.canvas);
